import { Piece, PieceType, PlayerColor } from "../../types";

type ChessPieceProps = {
  piece: Piece;
  type: PieceType;
  color: PlayerColor;
  square: number[];
  isBoardFlipped: boolean;
};

export const ChessPiece = ({
  piece,
  type,
  color,
  square,
  isBoardFlipped,
}: ChessPieceProps) => {
  const pieceColor = isBoardFlipped
    ? color === PlayerColor.WHITE
      ? PlayerColor.BLACK
      : PlayerColor.WHITE
    : color;
  return (
    <img
      className="h-3/4 z-10"
      src={`/assets/images/${color}-${type}.svg`}
      alt={`${type}`}
    />
  );
};
